:root {
  --warna-background: #b0d0de;
  --warna-card: #eaf0f5;
  --warna-border: #96bfce;
  --warna-putih: #ffffff;
  --warna-dalem: #9dc8d7;
}

:root {
  --font-inter: "Inter", sans-serif;
}

h1,
h2,
h3,
h4,
p {
  margin: 0;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  background-color: var(--warna-background);
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
}